<template>
  <v-card class="py-4" max-width="900">
    <v-card-title class="d-flex no-wrap flex-nowrap">
      <v-spacer />
      <div
        class="headline text-center text--lighten-2 keep-all font-weight-medium"
      >
        Automobile Service Quote
      </div>
      <v-spacer />
    </v-card-title>
    <v-divider class="mb-4" v-if="$vuetify.breakpoint.smAndDown" />
    <v-card-text>
      <v-form ref="form">
        <v-row>
          <v-col :cols="12" :xs="12" :sm="6" :md="6">
            <div class="black--text title mb-4">Contact Info</div>
            <v-row no-gutters>
              <v-col class="pr-1">
                <div class="black--text caption font-weight-medium">
                  FIRST NAME
                </div>
                <v-text-field
                  v-model="firstName"
                  dense
                  outlined
                  :rules="[rules.required]"
                />
              </v-col>
              <v-col>
                <div class="black--text caption font-weight-medium">
                  LAST NAME
                </div>
                <v-text-field
                  v-model="lastName"
                  dense
                  outlined
                  :rules="[rules.required]"
                />
              </v-col>
            </v-row>
            <v-row no-gutters>
              <v-col>
                <div class="black--text caption font-weight-medium">
                  COMPANY NAME
                </div>
                <v-text-field
                  v-model="form.companyName"
                  :rules="[rules.required]"
                  dense
                  outlined
                  hint="Enter 'self' if personal"
                  persistent-hint
                />
              </v-col>
            </v-row>
            <v-row no-gutters>
              <v-col>
                <div class="black--text caption font-weight-medium">
                  EMAIL
                </div>
                <v-text-field
                  v-model="form.email"
                  :rules="[rules.required, rules.email]"
                  dense
                  outlined
                />
              </v-col>
            </v-row>
            <v-row no-gutters>
              <v-col>
                <div class="black--text caption font-weight-medium">
                  PHONE NUMBER
                </div>
                <v-text-field
                  v-model="form.phoneNumber"
                  :rules="[rules.required]"
                  dense
                  outlined
                />
              </v-col>
            </v-row>
          </v-col>
          <v-col>
            <div class="black--text title mb-4">Routing Info</div>
            <v-row no-gutters>
              <v-col :cols="12" :xs="12" :sm="6" :md="6">
                <div class="black--text caption font-weight-medium">
                  ORIGIN COUNTRY
                </div>
                <v-text-field
                  v-model="form.originCountry"
                  :rules="[rules.required]"
                  dense
                  outlined
                />
              </v-col>
            </v-row>
            <v-row no-gutters>
              <v-col>
                <div class="black--text caption font-weight-medium">
                  ADDRESS OF ORIGIN
                </div>
                <v-text-field
                  v-model="form.addressOfOrigin"
                  :rules="[rules.required]"
                  dense
                  outlined
                />
              </v-col>
            </v-row>
            <v-row no-gutters>
              <v-col>
                <div class="black--text caption font-weight-medium">
                  DESTINATION COUNTRY
                </div>
                <v-text-field
                  v-model="form.destinationCountry"
                  :rules="[rules.required]"
                  dense
                  outlined
                />
              </v-col>
            </v-row>
            <v-row no-gutters>
              <v-col>
                <div class="black--text caption font-weight-medium">
                  ADDRESS OF DESTINATION
                </div>
                <v-text-field
                  v-model="form.addressOfDestination"
                  :rules="[rules.required]"
                  dense
                  outlined
                />
              </v-col>
            </v-row>
          </v-col>
        </v-row>

        <div class="black--text title">Freight Info</div>
        <!-- <v-row>
          <v-col>
            <div class="black--text caption font-weight-medium">
              DESCRIPTION OF FREIGHT CONTENT
            </div>
            <v-textarea
              v-model="form.freight.freightDescription"
              :rules="[rules.required]"
              dense
              outlined
            />
          </v-col>
        </v-row> -->

        <v-row>
          <v-col :cols="6" :xs="6" :sm="6" :md="3">
            <div class="black--text caption font-weight-medium">MAKE</div>
            <v-text-field
              v-model="form.freight.make"
              :rules="[rules.required]"
              dense
              outlined
            />
          </v-col>
          <v-col :cols="6" :xs="6" :sm="6" :md="3">
            <div class="black--text caption font-weight-medium">MODEL</div>
            <v-text-field
              v-model="form.freight.model"
              :rules="[rules.required]"
              dense
              outlined
            />
          </v-col>
          <v-col :cols="6" :xs="6" :sm="6" :md="3">
            <div class="black--text caption font-weight-medium">YEAR</div>
            <v-text-field
              v-model="form.freight.year"
              :rules="[rules.required]"
              dense
              outlined
            />
          </v-col>
          <v-col :cols="6" :xs="6" :sm="6" :md="3">
            <div class="black--text caption font-weight-medium">
              APPROXIMATE VALUE
            </div>
            <v-text-field
              v-model="form.freight.approximateValue"
              :rules="[rules.required]"
              prefix="$"
              suffix="USD"
              type="number"
              dense
              outlined
            />
          </v-col>
        </v-row>

        <v-row>
          <v-col :cols="12" :xs="12" :sm="6" :md="6">
            <div class="black--text caption font-weight-medium">
              SERVICE REQUIRED
            </div>
            <v-select
              v-model="form.freight.serviceRequired"
              style="max-width: 250px;"
              :rules="[rules.required]"
              single-line
              label="Select an option"
              :items="services"
              item-text="name"
              item-value="name"
              dense
              outlined
            />
          </v-col>
          <v-col :cols="12" :xs="12" :sm="12" :md="6">
            <div
              style="margin-bottom: -15px;"
              class="black--text caption font-weight-medium"
            >
              SHIPMENT TYPE
            </div>
            <v-checkbox
              hide-details
              dense
              color="black"
              multiple
              value="AIR FREIGHT"
              :rules="[rules.oneOrMore]"
              v-model="form.freight.shipmentType"
            >
              <template v-slot:label>
                <span class="font-weight-light black--text">Air Freight</span>
              </template>
            </v-checkbox>
            <v-checkbox
              hide-details
              dense
              color="black"
              multiple
              value="OCEAN FREIGHT-CONTAINERIZED"
              :rules="[rules.oneOrMore]"
              v-model="form.freight.shipmentType"
            >
              <template v-slot:label>
                <span class="font-weight-light black--text">
                  Ocean Freight - Containerized
                </span>
              </template>
            </v-checkbox>
            <v-checkbox
              hide-details
              dense
              color="black"
              multiple
              value="OCEAN FREIGHT-RORO"
              :rules="[rules.oneOrMore]"
              v-model="form.freight.shipmentType"
            >
              <template v-slot:label>
                <span class="font-weight-light black--text"
                  >Ocean Freight - Roll on roll off</span
                >
              </template>
            </v-checkbox>
          </v-col>
        </v-row>
      </v-form>
    </v-card-text>
    <v-card-actions class="pr-10">
      <v-spacer />
      <v-btn
        class="px-10"
        :color="sent ? 'success' : 'primary'"
        :loading="isSending"
        rounded
        @click="send()"
      >
        <template v-if="sent">
          <v-icon>done</v-icon>
          Sent
        </template>
        <template v-else>
          Send
        </template>
      </v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
import axios from 'axios'
const URL = 'https://vancouverglobalshipping.com/WebsiteFormEmailer'

export default {
  data() {
    return {
      sent: false,
      isSending: false,
      firstName: '',
      lastName: '',
      form: {
        service: 'Automobile Service Quote',
        fullName: '',
        companyName: '',
        email: '',
        phoneNumber: '',
        originCountry: '',
        addressOfOrigin: '',
        destinationCountry: '',
        addressOfDestination: '',
        freight: {
          serviceRequired: '',
          freightDescription: '',
          make: '',
          model: '',
          year: '',
          approximateValue: '',
          shipmentType: [],
        },
      },
      services: [
        { name: 'Port to Port' },
        { name: 'Port to Door' },
        { name: 'Door to Port' },
        { name: 'Door to Door - all inc.' },
      ],
      rules: {
        required: (v) => !!v || 'Required',
        oneOrMore: (v) => v && v.length > 0,
        email: (v) => !v || /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'E-mail must be valid'
      },
    }
  },
  filters: {
    uppercase: function (value) {
      if (!value) return ''
      value = value.toString()
      return value.toUpperCase()
    }
  },
  watch: {
    'form.make'(value) {
      this.form.make = this.$options.filters.uppercase(value)
    },
  },
  methods: {
    async send() {
      if (!this.$refs.form.validate()) {
        console.log('Form invalid')
        return
      }

      this.isSending = true

      try {
        this.form.fullName = `${ this.firstName } ${ this.lastName }`
        let response = await axios.post(URL, this.form)

        if (!response.data.err) {
          this.sent = true
          this.$emit('submitted')
          console.log(response)
        } else {
          console.error(response.data.err)
        }
      } catch (err) {
        console.error(err.toString());
      }

      this.isSending = false
    },

  },
}
</script>